.button-show,
.button-hide {
  position: relative;
  display: block;
  width: auto;
  margin: 5% 0 2% 10%;
  transition: color 0.3s;
}
.button-show:before,
.button-hide:before {
  position: absolute;
  content: "";
  background: url(../img/logo/expand.svg) no-repeat center center;
  background-size: contain;
  width: 15px;
  height: 15px;
  top: 10%;
  left: -35%;
  transition: transform 0.3s, opacity 0.3s;
}

.button-show:hover,
.button-hide:hover {
  color: #b5b6bc;
  transition: color 0.3s opacity 0.3s;
}

.button-hide:before {
  transform: rotate(180deg);
  left: -65%;
  opacity: 1;
}
.button-hide {
}
.button-hide:hover:before,
.button-show:hover:before {
  opacity: 50%;
}

.brand-wrapper {
  min-width: 320px;
  margin: auto;
  background-color: $color-background;

  &__text {
    width: 350px;
    height: fit-content;
    padding: 2rem 0 2rem 2rem;
    color: $color-gray-text;
    font-family: "TT Lakes";
    font-size: 16px;
    font-weight: 10;
    line-height: 24px;
    text-transform: uppercase;
  }

  &-container {
    height: fit-content;
    margin: 0;
    padding: 20px;
    background-color: #f9f9f9;
  }
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

.card img,
.brand-card img {
  height: 34px;
  width: auto;
}

.card .round,
.brand-card .round {
  height: 50px;
}

.card-button {
  position: relative;
  width: 40px;
  height: 40px;
  border: 2px solid $color-btn;
  border-radius: 50%;
  transition: transform 0.5s;

  &::before {
    content: "";
    position: absolute;
    top: 39%;
    left: 35%;
    width: 9px;
    height: 9px;
    border-top: 2.9px solid $color-btn;
    border-right: 2.9px solid $color-btn;
    border-radius: 2px 2px 2px 2px;
    transform: rotate(45deg);
  }

  &:hover {
    transform: scale(1.09);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    transition: box-shadow 0.5s transform 0.5s;
  }

  &:active {
    transform: scale(0.95);
    filter: brightness(90%);
  }
}

.button-show {
  display: none;

  @media (min-width: 768px) {
    position: relative;
    display: block;
    width: auto;
    margin: 5% 0 2% 10%;
    transition: color 0.3s;

    &:before {
      position: absolute;
      content: "";
      background: url(../img/ico/expand.svg) no-repeat center center;
      background-size: contain;
      width: 15px;
      height: 15px;
      top: 10%;
      left: -35%;
      transition: transform 0.3s, opacity 0.3s;
    }

    &:hover {
      color: #b5b6bc;
      transition: color 0.3s opacity 0.3s;
    }

    &:hover:before {
      opacity: 50%;
    }
  }
}

@media (min-width: 768px) {
  .brand-wrapper__text {
    width: 96%;
    height: 60px;
    margin-left: 4%;
  }

  .card,
  .brand-card {
    width: 31% !important;
    border: 1px solid #eaeaea;
    border-radius: 6px;
  }

  .brand-list {
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
    margin: 0 10px;
  }
}

@media (min-width: 1120px) {
  .brand-card {
    width: 23% !important;
  }
}

.brand-picture {
  width: 50%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right center;
  background-position: left;
}
.acer {
  background-image: url("../img/5.svg");
}
.sony {
  background-image: url("../img/6.svg");
}
.bosch {
  background-image: url("../img/3.svg");
}
.samsung {
  background-image: url("../img/7.svg");
}
.apple {
  height: 70%;
  background-image: url("../img/1.svg");
}
.lenovo {
  background-image: url("../img/8.svg");
}
