.services__text p,
.request-block p {
  width: 100%;
  color: #1b1c21;
}

.request-block {
  padding: 20px;
}
.request-block p {
  padding: 10px 0;
  color: #1b1c21;
}
.request-block a {
  position: relative;
  color: black;
  transition: color 0.3s;
  font-weight: bolder;
}
.request-block a::after {
  content: "";
  position: absolute;
  top: 39%;
  left: 103%;
  width: 8px;
  height: 8px;
  border-top: 2.9px solid #ff3e79;
  border-right: 2.9px solid #ff3e79;
  border-radius: 2px 2px 2px 2px;
  transform: rotate(45deg);
  opacity: 1;
  transition: opacity 0.3s;
}

.request-block a:hover {
  color: #b5b6bc;
  transition: color 0.3s opacity 0.3s;
}

.request-block a:hover::after {
  opacity: 50%;
}
.clients-button-repair,
.clients-button-checkstatus {
  height: 40px;
  margin-right: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right center;
  transition: filter 0.3s, transform 0.3s;
  border-radius: 50%;
  cursor: pointer;
}
.clients-button-repair {
  background-image: url(../img/logo/repair.svg);
}
.clients-button-checkstatus {
  background-image: url(../img/logo/checkstatus.svg);
}
.services__clients-buttons_item {
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f8f8f8;
  font-family: "TT Lakes Bold";
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
}
.services__about {
  display: flex;
  justify-content: center;

  align-items: flex-start;

  width: 100%;

  .services__info {
    flex: 1;
  }
  .services__text {
    width: 100%;
  }
  .services__img {
    flex: 1.2;
  }
  .services__img_file {
    width: 96%;
    padding: 2%;
  }
}

.request-block p {
  width: 70%;
}
.services {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: $color-background;
  padding: 20px 0;
  box-sizing: border-box;
  color: $color-gray-text;
  .services__head {
    display: flex;
    justify-content: space-between;
    padding: 0 0 0 5%;
  }
  @media (min-width: 1140px) {
    flex-grow: 1;
  }

  &__head {
    display: flex;
    align-items: center;
    height: 3rem;
    font-size: 1.2rem;
    margin: 10px 0;
    padding: 0 5%;
    border-left: 4px solid $color-accent;
    color: $color-text;

    & h1 {
      font-size: 2rem;
    }
  }

  &__clients-buttons {
    display: flex;
    justify-content: space-between;
    width: 40%;

    &__text {
      width: 170px;
      font-weight: bold;
      padding-top: 2%;
    }

    @media (max-width: 1440px) {
      display: none;
    }
  }

  &__nav {
    width: 100%;
    overflow: auto;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: none;
    -ms-overflow-style: none;
    margin: 20px 0;

    &::-webkit-scrollbar {
      width: 1px;
    }

    &--list {
      width: auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 20px;
      overflow-x: auto;

      &::-webkit-scrollbar {
        width: 0;
      }

      li a {
        color: $color-gray-text;
      }

      li {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        text-align: center;
        padding: 0 10px;
        height: 40px;
        font-size: 1rem;
        margin-right: 10px;
        border: 2px solid transparent;

        &:hover,
        &:active,
        &:focus {
          border: 2px solid $color-border;
          border-radius: 6px;
          background-color: white;
        }
      }
    }
  }

  &__reviews-button {
    display: none;
  }

  &__text {
    padding: 0 20px;

    p,
    .request-block p {
      width: 100%;
      color: $color-text;
    }

    &--hide {
      display: none;
    }
  }

  &__img_file {
    width: 100%;
    box-sizing: border-box;
  }
  .tablet-moretext {
    margin: 10px 0;
  }
  .expand-button {
    height: fit-content;
    display: flex;
    align-items: center;
    text-align: center;
    margin: 3% 0;

    &__item {
      display: block !important;
      position: relative;
      font-family: "TT Lakes";
      font-size: 1.1rem;
      background-color: $color-background;
      margin-left: 30px;

      &:hover {
        cursor: pointer;
      }
    }
  }
  .request-block {
    padding: 20px;
  }
  .request-block p {
    padding: 10px 0;
    color: #1b1c21;
  }
  .request-block a {
    position: relative;
    color: black;
    transition: color 0.3s;
    font-weight: bolder;
  }
  .request-block a::after {
    content: "";
    position: absolute;
    top: 39%;
    left: 103%;
    width: 8px;
    height: 8px;
    border-top: 2.9px solid #ff3e79;
    border-right: 2.9px solid #ff3e79;
    border-radius: 2px 2px 2px 2px;
    transform: rotate(45deg);
    opacity: 1;
    transition: opacity 0.3s;
  }

  .request-block a:hover {
    color: #b5b6bc;
    transition: color 0.3s opacity 0.3s;
  }

  .request-block a:hover::after {
    opacity: 50%;
  }

  .request-block {
    padding: 20px;

    @media (min-width: 768px) {
      &__nav--list {
        display: flex;
        justify-content: flex-start;
        padding-left: 50px;
      }

      &__about {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &__text {
        padding: 50px;

        &--hide {
          display: block;
          height: 50px;
          padding: 3% 0;
          overflow-y: hidden;
        }
      }

      &__about {
        display: flex;
        justify-content: space-between;
        font-size: 1.5rem;
      }

      &__nav--list li {
        font-size: 1.5rem;
        padding: 0 30px;
      }

      .request-block p {
        width: 60%;
      }
    }
    @media (max-width: 768px) {
      .request-block p {
        width: 80%;
      }
    }
    @media (min-width: 1120px) {
      header {
        display: none;
      }

      &__head {
        display: flex;
        justify-content: space-between;
        padding: 0 0 0 5%;
      }

      &__head h1 {
        font-size: 2.3rem;
        font-weight: bold;
      }

      .clients-button-repair,
      .clients-button-checkstatus {
        height: 40px;
        margin-right: 20px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: right center;
        transition: filter 0.3s, transform 0.3s;
        border-radius: 50%;
        cursor: pointer;
      }
      .clients-button-repair {
        background-image: url(../img/logo/repair.svg);
      }
      .clients-button-checkstatus {
        background-image: url(../img/logo/checkstatus.svg);
      }
      .services__clients-buttons_item {
        width: 10%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #f8f8f8;
        font-family: "TT Lakes Bold";
        font-size: 1.2rem;
        font-weight: bold;
        cursor: pointer;
      }

      &__clients-buttons_item {
        width: 10%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $color-background;
        font-family: "TT Lakes Bold";
        font-size: 1.2rem;
        font-weight: bold;
        cursor: pointer;
      }

      &__clients-buttons--img {
        margin-left: 5%;
      }

      &__text--hide {
        height: fit-content;
        overflow-y: visible;
      }

      &__reviews-button {
        display: block;
        font-family: "TT Lakes Bold";
        font-size: 24px;
        color: $color-gray;
        padding: 0 60px;
      }

      &__nav--list {
        display: flex;
        justify-content: space-between;
      }

      &__nav--list li {
        padding: 0 5px;
        font-size: 1.3rem;
      }
    }
  }
  @media (max-width: 768px) {
    .services__img_file {
      width: 100%;
    }
  }
}
#moretext {
  display: none;
}
