.swiper {
  width: 100%;
  height: 180px;
  background-color: #f8f8f8;
}

.swiper-slide-repair {
  height: 400px !important;
}

.card-repair {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin: 20px;

  .repair-service,
  .price,
  .duration {
    margin-bottom: 10px;
  }

  .duration {
    display: flex;
    align-items: center;

    .duration-text {
      margin-right: 10px;
    }

    .order-button {
      margin-left: auto;
    }
  }
}

@media (max-width: 768px) {
  .services__about {
    flex-direction: column;
    .services__info,
    .services__img {
      width: 100%;
    }
  }
}
.swiper.swiper-repair {
  height: 200px;
  .swiper-slide {
    border: none;
    background-color: #f8f8f8;
  }
  .card,
  .brand-card {
    flex-direction: column;
    width: 224px;
    height: 160px !important;
  }
  .card p {
    width: 90%;
    margin-top: 30px;
    padding: 0;
  }
}
.card-button {
  margin-left: 15%;
}
.card-button.card-button-repair {
  margin-left: 75%;
}

.swiper-slide,
.card,
.brand-card {
  width: 250px;
  height: 92px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #eaeaea;
  border-radius: 6px;
  padding: 10px;
  margin: 1%;
}
.brand-card.brand-card_repair {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 240px;
  height: 160px;
}

.swiper-slide {
  border: 1px solid #eaeaea;
  border-radius: 6px;
}
.swiper-pagination-bullet {
  width: 22px;
  height: 22px;
  margin: 0 8px;
  background-color: #dddddd;
  opacity: 0.8;
}

.swiper-pagination-bullet-active {
  background-color: #b5b6bc;
  opacity: 1;
}
@media (min-width: 768px) {
  .brand-wrapper__text {
    width: 96%;
    height: fit-content;
    margin-left: 4%;
  }
  .swiper {
    display: none;
  }
}
